  export default {
    name: 'mb',
    data (){
      return {
        name: '',
        tell: '',
        receivables_acount: '',//提现账号
        receivables_type: '',//方式
        placeholder: '请选择提现方式',
        opening_bank: '',//开户行
        captchaLabel:"获取验证码",
        captchaDisable: false,
        inputCode: '',
        options: ['支付宝', '银行卡'],
        value: '银行卡',
        list: {}
      }
    },
    methods: {
      getFxState(){
        this.http.get(this.ports.me.fxState, res =>{
          console.log(res)
          let msg = res.msg
          if(res.success){

          }else{
            if(res.code == 30002){
              this.$createDialog({
                type: 'confirm',
                icon: 'cubeic-alert',
                title: '',
                content: msg,
                confirmBtn: {
                  text: '去绑定',
                  active: true,
                  disabled: false,
                  href: 'javascript:;'
                },
                cancelBtn: {
                  text: '取消',
                  active: false,
                  disabled: false,
                  href: 'javascript:;'
                },
                onConfirm: () => {
                  this.$router.push('/reg/index')
                },
                onCancel: () => {
                  this.$router.go(-1)
                }
              }).show()
            }else{
              this.$createDialog({
                type: 'alert',
                title: '',
                content: msg,
                icon: 'cubeic-alert',
                onConfirm: () => {
                  this.$router.go(-1)
                },
              }).show()
            }
//            this.showToastTxtOnly(res.msg)
          }
        })
      },
      getMeData(){
        this.http.get(this.ports.me.index, res =>{
          this.$store.commit('changeLoading',false)
          console.log(res)
          if(res.success){
            let data = res.data
            this.list = data
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      subReseller(){
        let tellReg = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
        if(!this.name){
          this.showToastTxtOnly('请填写姓名')
          return false
        }
        if(!tellReg.test(this.tell)){
          this.showToastTxtOnly('请填写正确的手机号码')
          return false
        }
        if(this.value=='支付宝'&&!this.receivables_acount){
          this.showToastTxtOnly('请填写支付宝账号')
          return false
        }
        if(this.value=='银行卡'){
          if(!this.opening_bank){
            this.showToastTxtOnly('请填写开户行')
            return false
          }
          if(!this.receivables_acount){
            this.showToastTxtOnly('请填写银行卡账号')
            return false
          }
        }

        let params = {}
        params.name = this.name
        params.phone = this.tell
        params.receivables_type = this.value
        params.opening_bank = this.opening_bank
        params.receivables_acount = this.receivables_acount
        this.http.post(this.ports.me.distribution,params, res =>{
          this.$store.commit('changeLoading',false)
          console.log(res)
          if(res.success){
            let data = res.data
            this.count = data.count
            this.list = data.res
            this.showToastTxtOnly(res.msg)
            this.$router.push({path:'/me/index'})
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      }
    },
    mounted (){
      this.getMeData()
      this.getFxState()
    }
  }



